/* How It Works
/* -------------------- */
export const HowItWorks = `
# Purpose
We believe that Ethereums success is highly dependent on the number of individuals involved in the ecosystem. Our hope is to provide a platform for the creation of, and interaction with, Quests/Adventures - which are interactive tasks designed to give people their blockchain a-ha! moment(s). While the first version of this platform consists of 4 adventures established by the RAPID team, the next step will be enabling the community at large to build their own adventures using the tools we provide.

# The Adventure
Each Adventure is centered around a specific theme, and is comprised of multiple Quests. To complete a Quest, you will be required to perform the action outlined in the Quests description. Once complete, if the Quest involves a reward, you will submit evidence of quest completion (either your Ethereum address or a transaction hash) and receive a token certificate as a reward, if youve indeed completed the respective action. A token certificate is something developed by the RAPID team which allows the recipient of the certificate to redeem it for the underlying rewards (tokens!). Dont worry too much about how this works (unless youre a developer - then head over to our documentation to learn more!) - our platform will walk you through the process!

# The Rewards
By completing Quests, youll receive tokens! Initially, the tokens you receive will be DEV tokens. Only 1 million DEV tokens will ever be created; however, their value is yet unknown (besides their reputational value!). The main purpose of DEV token is to demonstrate the potential of the 1MD questing platform - in the future, when the platform is made more open and extensible, youll be able to create your own adventures, your own quests, and reward people with your own tokens!

# Vision of the Future
As mentioned, our goal is to turn this platform into a tool which allows individuals / teams from around the ecosystem to easily create their own quests. Through this, well end up with a community-curated educational/informational experience that can introduce individuals from both inside and outside of the ecosystem to all of the amazing things happening on Ethereum. For example, a MakerDAO adventure might be created by the MakerDAO team to introduce users to their platform. Alternatively, a user may create an overarching DeFi adventure which includes interaction with MakerDAO. These adventures/quests will be both created and ranked by the community, making this an extremely collaborate initiative which needs your help to succeed!
`
